<template>
  <div class="categories">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showaddCartDialog"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <tree-table
        :data="cateList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
        class="TreeTab"
      >
        <!--  -->
        <template slot="isok" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color:lightgreen;"
          ></i>
          <i class="el-icon-error" v-else style="color:red;"></i>
        </template>
        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag type="success" size="mini" v-else-if="scope.row.cat_level === 1"></el-tag>
          <el-tag type="warning" size="mini" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="opt">
          <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
        </template>
      </tree-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!--  -->
      <el-dialog title="" :visible.sync="categoriesVisible" width="50%" @close="addDelete">
        <!--  -->
        <el-form
          :model="addCartForm"
          :rules="addCartFormRules"
          ref="addCartFormRef"
          label-width="100px"
        >
          <el-form-item label="" prop="cat_name">
            <el-input v-model="addCartForm.cat_name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <!-- options -->
            <el-cascader
              v-model="selectedKeys"
              :options="parentCartList"
              expand-trigger="hover"
              :props="cascad"
              @change="adChange"
              clearable
            ></el-cascader>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="categoriesVisible = false"> </el-button>
          <el-button type="primary" @click="addCart"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      cateList: [],
      // 
      total: 0,
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      columns: [
        {
          label: "",
          prop: "cat_name"
        },
        {
          label: "",
          type: "template",
          template: "isok"
        },
        {
          label: "",
          type: "template",
          template: "order"
        },
        {
          label: "",
          type: "template",
          template: "opt"
        }
      ],
      categoriesVisible: false,
      // 
      addCartForm: {
        // 
        cat_name: "",
        // id
        cat_pid: 0,
        // 
        cat_level: 0
      },
      // 
      addCartFormRules: {
        cat_name: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      //
      parentCartList: [],
      //
      cascad: {
        value: "cat_id",
        label: "cat_name",
        children: "children"
      },
      //id
      selectedKeys: []
    };
  },
  created() {
    // 
    this.getCateList();
  },
  methods: {
    //
    async getCateList() {
      let { data: res } = await this.$http.get("categories", {
        params: this.queryInfo
      });
      if (res.meta.status !== 200) {
        this.$message.error("");
      } else {
        // console.log(res)
        this.cateList = res.data.result;
        // 
        this.total = res.data.total;
      }
    },
    // pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      // 
      this.getCateList();
    },
    // pagenum
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      // 
      this.getCateList();
    },
    //
    showaddCartDialog() {
      // 
      this.getPanentCateList();
      this.categoriesVisible = true;
    },
    // 
    async getPanentCateList() {
      let { data: res } = await this.$http.get("categories", {
        params: { type: 2 }
      });
      if (res.meta.status !== 200) {
        this.$message.error("");
      } else {
        //  console.log(res.data)
        this.parentCartList = res.data;
      }
    },
    // 
    adChange() {
      if (this.selectedKeys.length > 0) {
        //id
        this.addCartForm.cat_pid = this.selectedKeys[
          this.selectedKeys.length - 1
        ];
        //  
        this.addCartForm.cat_level = this.selectedKeys.length;
        return;
      } else {
        this.addCartForm.cat_pid = 0;
        this.addCartForm.cat_level = 0;
      }
    },
    // 
    addCart() {
      this.$refs.addCartFormRef.validate(async valid => {
        if (!valid) return;
        let { data: res } = await this.$http.post(
          "categories",
          this.addCartForm
        );
        if (res.meta.status !== 201) {
          this.$message.error("");
        } else {
          this.$message.success("");
          // 
          this.getCateList();
          this.categoriesVisible = false;
        }
      });
    },
    // 
    addDelete() {
      this.$refs.addCartFormRef.resetFields();
      this.selectedKeys = [];
      this.addCartForm.cat_pid = 0;
      this.addCartForm.cat_level = 0;
    }
  }
};
</script>

<style lang="less">
.TreeTab {
  margin-top: 15px;
}
.el-cascader {
  width: 100%;
}
.el-cascader-menu__list {
  height: 200px;
  font-size: 14px;
}
</style>