<template>
  <div class="order">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query">
            <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price"></el-table-column>
        <el-table-column label="" prop="pay_status">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status === '1'"></el-tag>
            <el-tag type="danger" v-else-if="scope.row.pay_status === '0'"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send"></el-table-column>
        <el-table-column label="" prop="create_time">
          <template slot-scope="scope">{{ scope.row.create_time | dateFormat }}</template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showOrder"></el-button>
            <el-button type="success" icon="el-icon-location" size="mini" @click="showOrderBox"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 8, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="orderVisible" width="50%">
      <el-form
        :model="addOrderForm"
        :rules="addOrderRules"
        ref="addOrderRuleForm"
        label-width="100px"
      >
        <el-form-item label="/" prop="addOrder1">
          <el-cascader v-model="addOrderForm.addOrder1"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="addOrder2">
          <el-input v-model="addOrderForm.addOrder2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="orderVisible = false"> </el-button>
        <el-button type="primary" @click="orderVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="wuliuVisible" width="50%">
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in wuliuList"
          :key="index"
          :timestamp="activity.time"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
      <span slot="footer" class="dialog-footer">
        <el-button @click="wuliuVisible = false"> </el-button>
        <el-button type="primary" @click="wuliuVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import area from "./area";
export default {
  data() {
    return {
      // 
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 5
      },
      // 
      orderList: [],
      // 
      total: 0,
      //
      orderVisible: false,
      addOrderForm: {
        addOrder1: [],
        addOrder2: ""
      },
      addOrderRules: {
        addOrder1: [
          { required: true, message: "/", trigger: "blur" }
        ],
        addOrder2: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      wuliuVisible: false,
      wuliuList: []
    };
  },
  created() {
    // 
    this.getOrderList();
  },
  methods: {
    // 
    async getOrderList() {
      let { data: res } = await this.$http.get("orders", {
        params: this.queryInfo
      });
      if (res.meta.status !== 200) {
        this.$message.error("");
      } else {
        this.orderList = res.data.goods;
        this.total = res.data.total;
      }
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      // 
      this.getOrderList();
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      // 
      this.getOrderList();
    },
    // 
    showOrder() {
      this.orderVisible = true;
    },
    // 
    async showOrderBox() {
      let { data: res } = await this.$http.get("/kuaidi/1106975712662");
      if (res.meta.status !== 200) {
        this.$message.error("");
      } else {
        this.wuliuList = res.data;
        this.wuliuVisible = true;
        // console.log(this.wuliuList);
      }
    },
    // 
    shuContent(){
      alert(123)
    }
  }
};
</script>

