<template>
  <div class="users">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="dialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="userlist" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="mobile"></el-table-column>
        <el-table-column label="" prop="role_name"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="userStateChange(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showEdit(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="open(scope.row.id)"></el-button>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <!--  -->
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="fenpei(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 3, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisible" width="50%" @close="addDial">
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
          <el-form-item label="" prop="username">
            <el-input v-model="addForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="addForm.password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="addForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="addForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="editVisible" width="50%" @close="EditDial">
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
          <el-form-item label="">
            <el-input v-model="editForm.username" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="editForm.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobile">
            <el-input v-model="editForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editVisible = false"> </el-button>
          <el-button type="primary" @click="editUserInfo"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="userVisible" width="50%" @close="setuserClose">
        <p>{{ userInfo.username }}</p>
        <p>{{ userInfo.role_name}}</p>
        <p>
          
          <el-select v-model="selectRoleId" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            ></el-option>
          </el-select>
        </p>
        <span slot="footer" class="dialog-footer">
          <el-button @click="userVisible = false"> </el-button>
          <el-button type="primary" @click="fenpeiquedeng"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    // 
    var checkEaild = (rule, value, cb) => {
      const regEmail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (regEmail.test(value)) {
        // 
        return cb();
      } else {
        cb(new Error(""));
      }
    };
    // 
    var checkMobile = (rule, value, cb) => {
      const regMobile = /^1[3456789]\d{9}$/;
      if (regMobile.test(value)) {
        // 
        return cb();
      } else {
        cb(new Error(""));
      }
    };
    return {
      queryInfo: {
        query: "",
        // 
        pagenum: 1,
        // 
        pagesize: 3
      },
      userlist: [],
      total: 0,
      dialogVisible: false,
      //
      addForm: {
        username: "",
        password: "",
        email: "",
        mobile: ""
      },
      //
      addFormRules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "3~10",
            trigger: "blur"
          }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: "6~15",
            trigger: "blur"
          }
        ],
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEaild, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" }
        ]
      },
      editVisible: false,
      // 
      editForm: {},
      //   
      editFormRules: {
        email: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkEaild, trigger: "blur" }
        ],
        mobile: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" }
        ]
      },
      // 
      userVisible: false,
      // 
      userInfo: {},
      // 
      rolesList: [],
      // 
      selectRoleId: ""
    };
  },
  created() {
    //   
    this.getUserList();
  },
  methods: {
    async getUserList() {
      let { data: res } = await this.$http.get("users", {
        params: this.queryInfo
      });
      if (res.meta.status !== 200) {
        this.$message.error("");
      } else {
        this.userlist = res.data.users;
        this.total = res.data.total;
        // console.log(res);
      }
    },
    // pagesize 
    handleSizeChange(newSize) {
      //   console.log(newSize);
      this.queryInfo.pagesize = newSize;
      //   
      this.getUserList();
    },
    //
    handleCurrentChange(newPage) {
      //   console.log(newPage);
      this.queryInfo.pagenum = newPage;
      //   
      this.getUserList();
    },
    // 
    async userStateChange(userinfo) {
      console.log(userinfo);
      let { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      );
      if (res.meta.status !== 200) {
        this.$message.error("");
        userinfo.mg_state = !userinfo.mg_state;
      } else {
        this.$message.success("");
      }
    },
    // 
    addDial() {
      this.$refs.addFormRef.resetFields();
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return;
        } else {
          //
          let { data: res } = await this.$http.post("users", this.addForm);
          if (res.meta.status !== 201) {
            this.$message.error("!");
          } else {
            this.$message.success("");
            //
            this.dialogVisible = false;
            // 
            this.getUserList();
          }
        }
      });
    },
    // 
    async showEdit(id) {
      let { data: res } = await this.$http.get("users/" + id);
      if (res.meta.status !== 200) {
        this.$message.error("");
      } else {
        this.editForm = res.data;
        this.editVisible = true;
      }
    },
    //
    EditDial() {
      this.$refs.editFormRef.resetFields();
    },
    // 
    editUserInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) {
          return;
        } else {
          let { data: res } = await this.$http.put(
            "users/" + this.editForm.id,
            { email: this.editForm.email, mobile: this.editForm.mobile }
          );
          if (res.meta.status !== 200) {
            return this.$message.error("");
          } else {
            this.editVisible = false;
            this.$message.success(res.meta.msg);
            //   
            this.getUserList();
          }
        }
      });
    },
    // 
    async open(id) {
      const cfom = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      if (cfom !== "confirm") {
        return this.$message.info("");
      } else {
        let { data: res } = await this.$http.delete("users/" + id);
        if (res.meta.status !== 200) {
          return this.$message.error("");
        } else {
          this.$message.success(res.meta.msg);
          //   
          this.getUserList();
        }
      }
    },
    // 
    async fenpei(userInfo) {
      this.userInfo = userInfo;
      let { data: res } = await this.$http.get("roles");
      if (res.meta.status !== 200) {
        this.$message.error("");
      } else {
        this.rolesList = res.data;
      }
      this.userVisible = true;
    },
    // f
    async fenpeiquedeng() {
      if (!this.selectRoleId) {
        this.$message, error("");
      } else {
        let {
          data: res
        } = await this.$http.put(`users/${this.userInfo.id}/role`, {
          rid: this.selectRoleId
        });
        if (res.meta.status !== 200) {
          this.$message.error("");
        } else {
          this.$message.success("");
          //   
          this.getUserList();
          this.userVisible = false
        }
      }
    },
    // 
    setuserClose(){
      this.selectRoleId = ''
      this.userInfo = {}
    }
  }
};
</script>

