<template>
  <div class="add">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--   -->
      <el-alert title="" type="info" :closable="false" center show-icon></el-alert>
      <!--  -->
      <el-steps :space="200" align-center :active="activeIndex - 0" finish-status="success">
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!-- tab -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs
          v-model="activeIndex"
          :tab-position="'left'"
          :before-leave="beforeTableLeave"
          @tab-click="tabClicked"
        >
          <!--  -->
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <!--  -->
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                v-model="addForm.goods_cat"
                :options="cateList"
                :props="cateProps"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="1">
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="cb" v-for="(cb,i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <el-input :v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!-- action api -->
            <el-upload
              :action="upLoadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :headers="headerObj"
              :on-success="headleSuccess"
              list-type="picture"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4"></el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible" width="50%">
      <img :src="previewPath" alt class="previewImg" />
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeIndex: "0",
      //    
      manyTableData: [],

      previewVisible: false,
      onlyTableData: [],
      upLoadURL: "http://127.0.0.1:8888/api/private/v1/upload",
      //   headers
      headerObj: {
        Authorization: window.sessionStorage.getItem("token")
      },
      //    
      previewPath: "",
      addForm: {
        goods_name: "",
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        //    
        goods_cat: [],
        // 
        pics: [],
        //    
        goods_introduce: ""
      },
      addFormRules: {
        goods_name: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_price: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_weight: [
          { required: true, message: "", trigger: "blur" }
        ],
        goods_number: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      //    
      cateList: [],
      cateProps: {
        label: "cat_name",
        value: "cat_id",
        children: "children"
      }
    };
  },
  created() {
    this.getCateList();
    this.tabClicked();
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get("categories");
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }

      this.cateList = res.data;
      // console.log(res.data)
    },
    //   
    handleChange() {
      //  console.log(this.addForm.goods_cat)
      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = [];
      }
    },
    beforeTableLeave(activeName, oldActiveName) {
      if (oldActiveName === "0" && this.addForm.goods_cat.length !== 3) {
        this.$message.error("");
        return false;
      }
    },
    async tabClicked() {
      //  
      if (this.activeIndex === "1") {
        const {
          data: res
        } = await this.$http.get(`categories/${this.cateId}/attributes`, {
          params: { sel: "many" }
        });
        // console.log(11111);
        if (res.meta.status !== 200) {
          return this.$message.error("");
        }
        // console.log(res.data);

        res.data.forEach(item => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(",");
        });
        this.manyTableData = res.data;
      } else if (this.activeIndex === "2") {
        const {
          data: res
        } = await this.$http.get(`categories/${this.cateId}/attributes`, {
          params: { sel: "only" }
        });
        // console.log(22222);
        if (res.meta.status !== 200) {
          return this.$message.error("");
        }
        // console.log(res.data);
        this.onlyTableData = res.data;
      }
    },
    //  
    handlePreview(file) {
    //   console.log(file);
      this.previewPath = file.response.data.url;
      this.previewVisible = true;
    },
    // 
    handleRemove(file) {
      // console.log(file)
      const filePath = file.response.data.tmp_path;
      const i = this.addForm.pics.findIndex(x => x.pic === filePath);
      this.addForm.pics.splice(i, 1);
    //   console.log(this.addForm);
    },
    // 
    headleSuccess(response) {
      // 
      const picInfo = { pic: response.data.tmp_path };
      //  pushpics
      this.addForm.pics.push(picInfo);
    //   console.log(this.addForm);
    }
  },
  computed: {
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2];
      }
      return null;
    }
  }
};
</script>

<style>
.el-checkbox {
  margin: 0px 10px 0px 0px !important;
}
.previewImg {
  width: 100%;
}
.btnAdd {
  margin-top: 15px;
}
</style>