<template>
  <div class="goods">
        <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

        <!--  -->
    <el-card>
        <el-row>
            <el-col :span="8" class="secc">
                  <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodsList">
                        <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
                  </el-input>
            </el-col>
            <el-col :span="4">
                <el-button type="primary" @click="goAddPage"></el-button>
            </el-col>
        </el-row>

        <!-- table -->
        <el-table :data="goodslist" border stripe>
            <el-table-column type="index"></el-table-column>
            <el-table-column label="" prop="goods_name"></el-table-column>
            <el-table-column width="120px" label="" prop="goods_price"></el-table-column> 
            <el-table-column  width="70px" label="" prop="goods_weight"></el-table-column>
            <el-table-column  width="160px" label="" prop="add_time">
                <template slot-scope="scope">
                    {{ scope.row.add_time | dateFormat}}
                </template>
            </el-table-column>
            <el-table-column width="180px"  label="" >
                <template slot-scope="scope">
                    <el-button type="primary" icon="el-icon-edit" size="mini" ></el-button>
                    <el-button type="danger" icon="el-icon-delete"  size="mini"  @click="removeById(scope.row.goods_id)" ></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[5, 10, 15, 20]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
             background
            >
        </el-pagination>
    </el-card>

  </div>
</template>

<script>
export default {
    data() {
        return {
            // 
            queryInfo:{
                query:'',
                pagenum:1,
                pagesize:10
            },
            // 
            goodslist:[],
                total:0,
            

        }
    },
    created() {
        this.getGoodsList()
    },
    methods: {
        // 
    async  getGoodsList(){
        const {data:res} = await this.$http.get('goods',{params:this.queryInfo})
        if(res.meta.status !==200){
             return this.$message.error('')
        }
        // console.log(res)
        this.goodslist=res.data.goods
        this.total = res.data.total
     },
     handleSizeChange(newSize){
         this.queryInfo.pagesize = newSize
         this.getGoodsList()
     },
     handleCurrentChange(newPage){
         this.queryInfo.pagenum= newPage
         this.getGoodsList()
     },
    //    
   async  removeById(id){
     const confirmResult =  await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err=>err)

        if(confirmResult !='confirm'){
            return this.$message.info('')
        }

     const{data:res} = await  this.$http.delete(`goods/${id}`)

        if(res.meta.status !==200){
            this.$$message.error('')
        }
        this.$message.success("")
        // 
        this.getGoodsList()
    },
    // 
    goAddPage(){
        this.$router.push('/goods/add')
    }
   },
}
</script>

<style>
.secc{
  margin-right: 15px;
}
</style>