<template>
  <div class="params">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="warning" :closable="false" show-icon></el-alert>
      <!--  -->
      <el-row class="cat_opt">
        <el-col>
          <span></span>
          <!-- -->
          <el-cascader
            v-model="seletKeys"
            expand-trigger="hover"
            :options="paramsList"
            @change="paramsChange"
            :props="addProps"
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- tab -->
      <el-tabs v-model="activeName" @tab-click="handleTabClick" class="tabl">
        <!--  -->
        <el-tab-pane label="" name="first">
          <el-button type="primary" size="mini"></el-button>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="second">
          <el-button type="primary" size="mini"></el-button>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      paramsList: [],
      //props
      addProps: {
        value: "cat_id",
        lable: "cat_name",
        children: "children"
      },
      //
      seletKeys: [],
      // 
      activeName: "first"
    };
  },
  created() {
    // 
    this.getParamsList();
  },
  methods: {
    // 
    async getParamsList() {
      let { data: res } = await this.$http.get("categories");
      if (res.meta.status !== 200) {
        this.$message.error("");
      } else {
        this.paramsList = res.data;
        // console.log(this.paramsList)
      }
    },
    // 
    paramsChange() {
      // console.log(this.seletKeys);
    },
    // tab
    handleTabClick() {
      console.log(this.activeName);
    },
    // true,false
    isTabDisabled(){
      if(this.seletKeys.length !== 3){
        return true
      }
        return false
      
    }
  }
};
</script>

<style scoped>
.cat_opt {
  margin-top: 15px;
}
.el-cascader-menu__list {
  height: 200px;
  font-size: 14px;
}
.el-cascader {
  width: 20%;
}
.tabl{
  margin-top: 15px;
}
</style>