<template>
  <div class="roles">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="rolesList" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              v-for="(item,index) in scope.row.children"
              :key="item.id"
              :class="['bdbottom',index==0?'bdtop':'','vcenter']"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRoles(scope.row,item.id)">{{ item.authName }}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!-- for -->
                <el-row
                  v-for="(item2,i2) in item.children"
                  :class="[i2 == 0 ? '':'bdtop','vcenter']"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRoles(scope.row,item2.id)"
                    >{{ item2.authName }}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!-- for -->
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRoles(scope.row,item3.id)"
                    >{{ item3.authName }}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
            <!-- <pre>{{ scope.row }}</pre> -->
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" icon="el-icon-edit"></el-button>
            <el-button type="danger" size="mini" icon="el-icon-delete" @click="open(scope.row.id)"></el-button>
            <el-button
              type="warning"
              size="mini"
              icon="el-icon-setting"
              @click="showSetRolesDialog(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="setRolesDialogVisible" width="50%" @close="setRolesDialogClosed">
      <!--  -->
      <el-tree
        :data="rightList"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRolesDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allutRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rolesList: [],
      // 
      setRolesDialogVisible: false,
      rightList: [],
      //
      treeProps: {
        children: "children",
        label: "authName"
      },
      // key 
      defKeys: [],
      roleId:''
    };
  },
  created() {
    // 
    this.getRolesList();
  },
  methods: {
    // 
    async getRolesList() {
      let { data: res } = await this.$http.get("roles");
      if (res.meta.status !== 200) {
        this.$message.error("");
      } else {
        this.rolesList = res.data;
        //   console.log(res)
      }
    },
    // id
    async removeRoles(role, rightsId) {
      const confirmRoles = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      if (confirmRoles !== "confirm") {
        this.$message.info("");
      } else {
        let { data: res } = await this.$http.delete(
          `roles/${role.id}/rights/${rightsId}`
        );
        if (res.meta.status !== 200) {
          this.$message.error("!");
        } else {
          role.children = res.data;
        }
      }
    },
    // 
    async showSetRolesDialog(role) {
        this.roleId = role.id
      let { data: res } = await this.$http.get("rights/tree");
      if (res.meta.status !== 200) {
        this.$message.error("");
      } else {
        this.rightList = res.data;
      }
    // id
    this.getLeafKeys(role,this.defKeys)
      this.setRolesDialogVisible = true;
    },
    // iddefKeys
    getLeafKeys(node,arr){
        // nodechildren
        if(!node.children){
            arr.push(node.id)
        }else{
            node.children.forEach(item =>{
                this.getLeafKeys(item,arr)
            })
        }
    },
    // id
    setRolesDialogClosed(){
        this.defKeys = []
    },
    // 
   async allutRights(){
        const keys = [
            ...this.$refs.treeRef.getCheckedKeys(),
            ...this.$refs.treeRef.getHalfCheckedNodes()
        ]
        const idStr = keys.join(',')
       let { data:res } = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
       if(res.meta.status !== 200){
           this.$message.error('')
       }else{
            // 
           this.getRolesList()
           this.setRolesDialogVisible = false
       }
    },
    //
    open(id){
      console.log(id)
    }
  }
};
</script>

<style lang="less">
.roles {
  .el-tag {
    margin: 7px;
  }
  .bdtop {
    border-top: 1px solid #eee;
  }
  .bdbottom {
    border-bottom: 1px solid #eee;
  }
}
</style>